$ic-deadpool: 0px 0px 111px 111px;
$ic-ghost-rider: 0px -111px 111px 111px;
$ic-spiderman: 0px -222px 111px 111px;
$ic-wolverine: 0px -333px 111px 111px;

@mixin sprite-width($sprite) {
  width: nth($sprite, 3);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 4);
}

@function sprite-width($sprite) {
  @return nth($sprite, 3);
}

@function sprite-height($sprite) {
  @return nth($sprite, 4);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 1);
  $sprite-offset-y: nth($sprite, 2);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite($sprite) {
  @include sprite-position($sprite);
  background-repeat: no-repeat;
  overflow: hidden;
  display: inline-block;
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

.ic-deadpool {
  @include sprite($ic-deadpool);
  background-image: url('ics-marvel.png');
}

.ic-ghost-rider {
  @include sprite($ic-ghost-rider);
  background-image: url('ics-marvel.png');
}

.ic-spiderman {
  @include sprite($ic-spiderman);
  background-image: url('ics-marvel.png');
}

.ic-wolverine {
  @include sprite($ic-wolverine);
  background-image: url('ics-marvel.png');
}




