$ic-captain-america: 0px 0px 111px 111px;
$ic-hulk: 0px -111px 111px 111px;
$ic-ironman: 0px -222px 111px 111px;
$ic-thor: 0px -333px 111px 111px;

@mixin sprite-width($sprite) {
  width: nth($sprite, 3);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 4);
}

@function sprite-width($sprite) {
  @return nth($sprite, 3);
}

@function sprite-height($sprite) {
  @return nth($sprite, 4);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 1);
  $sprite-offset-y: nth($sprite, 2);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite($sprite) {
  @include sprite-position($sprite);
  background-repeat: no-repeat;
  overflow: hidden;
  display: inline-block;
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

.ic-captain-america {
  @include sprite($ic-captain-america);
  background-image: url('ics-avengers.png');
}

.ic-hulk {
  @include sprite($ic-hulk);
  background-image: url('ics-avengers.png');
}

.ic-ironman {
  @include sprite($ic-ironman);
  background-image: url('ics-avengers.png');
}

.ic-thor {
  @include sprite($ic-thor);
  background-image: url('ics-avengers.png');
}




